import React from "react";
import { useDispatch } from "react-redux";
import styled from "styled-components";
import { deleteProductAction } from "../../../state";

const StyledProductItem = styled.li`
  margin-top: 25px;
  background-color: white;
  border-radius: 3px;
  padding: 1em;
  box-shadow: 1px 1px 8px 1px rgba(0, 0, 0, 0.25);
  img {
    float: left;
    border: 1px solid black;
    width: 70px;
    height: 100%;
  }
  &:last-child {
    margin-bottom: 25px;
  }
`;

interface Props {
  id: number;
  imgUrl: string;
  productName: string;
  productDescription: string;
  productCount: string;
}

export default function ProductItem({
  id,
  imgUrl,
  productName,
  productDescription,
  productCount,
}: Props) {
  const dispatch = useDispatch();

  return (
    <StyledProductItem className="productItem">
      <div className="product">
        <img src={imgUrl} alt="product" />
        <span className="productName">{productName}</span>
        <br />
        <p className="productDescription">{productDescription}</p>
        <span className="productCount">{productCount}</span>
      </div>
      <button>Details</button>
      <button onClick={() => dispatch(deleteProductAction(id))}>Delete</button>
    </StyledProductItem>
  );
}
