import React from "react";
import ProductItem from "./components/ProductItem";
import { StyledProductsList } from "./StyledProductsList";
import { allProductsSelector } from "../../state";
import { useSelector } from "react-redux";

export default function ProductsList() {
  const products = useSelector(allProductsSelector);
  return (
    <StyledProductsList>
      {products.map((item) => (
        <ProductItem
          id={item.id}
          imgUrl={item.imageUrl}
          productName={item.name}
          productDescription={`Size: Width: ${item.size.width} Height: ${item.size.height}`}
          productCount={`Count: ${item.count}`}
        />
      ))}
    </StyledProductsList>
  );
}
